import React, { Component } from "react";
import { ZebulonTableAndConfiguration } from "../table/ZebulonTableAndConfiguration";
import { meta } from "./dataset.meta";
import { get_array } from "./datasources";
import { ResizableBox } from "react-resizable";

// customMenuFunctions;

export class MyDatasetConfiguration extends Component {
  constructor(props) {
    super(props);
    const metaDataset = { ...meta };
    metaDataset.table = { ...metaDataset.table };
    metaDataset.table.actions = [
      ...metaDataset.table.actions,
      {
        type: "action",
        caption: "Export Meta",
        enable: true,
        hidden: true,
        action: "exportMeta"
      }
    ];
    metaDataset.properties = [];
    metaDataset.lockedIndex = null;
    metaDataset.lockedWidth = null;
    const data = get_array({ noJoins: true });
    const status = {};
    this.state = {
      status,
      data,
      meta: metaDataset,
      params: { user_: "Zebulon" },
      sizes: {
        height: 500,
        width: 1300,
        rowHeight: 25,
        zoom: 1
      }
    };
  }
  onResize = (e, data) => {
    this.setState({
      sizes: {
        ...this.state.sizes,
        height: data.size.height,
        width: data.size.width
      }
    });
  };
  render() {
    const text =
      "Dataset row object:{d:<date>,id:<number>,product_id:<number>,currency_id:<number>,country_id:<number>,qty:<number>,color:<string>,thirdparty:{id:<number>,cd:<string>,lb:<string>}}.\nProduct object:{id:<number>,label:<string>,price:<number>,shape:<string>,size:<string>}.\nCurrency object:{id:<number>,code:<string>,label:<string>,symbol:<string>rate:<number>}.\nCountry object:{id:<number>,code:<string>,label:<string>,currency_id:<number>,vat:<number>}.\nN.B. Accessors, formats...exists allready.\n\nOn the Properties tab.\nJoined the objects products, countries, currencies:\nâ€¢ create 3 new properties (product, country, currency) with \n  â€¢ type = joined object.\n  â€¢ accessor = respectively row.product_id, row.country_id, row.currency_id.\n  â€¢ select = respectively getProducts, getCountries, currencies.Apply changes on the dataset (apply button) to load the instances of the objects, new accessors on objects keys won't be accessible otherwise.\n\nOn the Dataset tab.\n\nOn the Properties tab.\nCreate the 4 new properties for products:\nâ€¢ product_cd, data type = string, editable = true, accessor = row.product.label (only the properties used for the select Input must be editable)\nâ€¢ price, data type = number, editable = false, accessor = row.product.price\nâ€¢ shape, data type = string, editable = false, accessor = row.product.shape\nâ€¢ size, data type = string, editable = false, accessor = row.product.size\nSet the price format to price property,\nCreate the 2 new properties for countries:\nâ€¢ country_cd, data type = string, editable = true, accessor = row.country.code (only the properties used for the select Input must be editable)\nâ€¢ flag, editable = false, accessor = flag, format = image, width = 40\nCreate the new property for currencies:\nâ€¢ currency_cd, data type = string, editable = true, accessor = row.currency.code (only the properties used for the select Input must be editable)\nCreate the new property for thirdparties foreign table object:\nâ€¢ thirdparty_cd, data type = string, editable = true, accessor = row.thirdparty.cd, foreign object = thirdparties\nSet row color select = getColors, data type = string.\nCreate a new property:\nâ€¢ code = month, data type = date, format = mm/yyyy, accessor = mth, filter = values\nHide the ids (product, currency, country).\nCreate the 2 calculated properties \nâ€¢ amount, data type = number, editable =  false, format = amt_cur, accessor = amt_cur, width = 150\nâ€¢ amountEUR, data type = number, editable =  false, format = amt_â‚¬, accessor = amt_â‚¬, width = 150.\nApply changes and check on the Dataset tab\n\nOn the Functions tab.\nCreate a new accessor: gross_amount.\nâ€¢ code = gross_amt.\nâ€¢ visibility = dataset.\nâ€¢ type = accessor.\nâ€¢ function = ({row})=>accessor('amt_cur')({row})*(1+(row.country||{vat:0}).vat).\nN.B. The accessor function let you use an other accessor in your function.\nApply changes and check on the Dataset tab.\n\nOn the Properties tab.\nCreate a new property:\nâ€¢ code = gross_amt.\nâ€¢ format = amt_cur.\nâ€¢ accessor = gross_amt.\nCreate the 2 new properties calculated by analytics functions:\nâ€¢ code = totalAmountByCountry, data type = number, accessor = totalAmt, analytic function = total_amt.\nâ€¢ code = rollingAverage, data type = number, accessor = amt_cur, analytic function = rolling_avg.\nApply changes and check on the Dataset tab. \n\nAdd an init function to set country currency by default when country changes and currency is null (country_cd/onChange = onChangeCountry).\nRename the columns (caption column).\nSet the appropriate filter type on the properties..\nApply changes and check on the Dataset tab.\n.\nTest the result on the Dataset tab.";
    const { keyEvent, functions, id } = this.props;
    // ({row})=>{const acc=accessor('amt_cur');return acc({row})*(1+(row.country||{vat:0}).vat);}
    const { data, meta, status, params } = this.state;
    return (
      <div>
        <ResizableBox
          height={this.state.sizes.height}
          width={this.state.sizes.width}
          onResize={this.onResize}
        >
          <ZebulonTableAndConfiguration
            id={id}
            functions={functions}
            params={params}
            data={data}
            meta={meta}
            status={status}
            sizes={{
              ...this.state.sizes,
              height: this.state.sizes.height - 30
            }}
            keyEvent={keyEvent}
            scrollbarsOnBox={this.props.scrollbarsOnBox}
            // errorHandler={this.errorHandler}
          />
        </ResizableBox>
        <div
          style={{
            fontFamily: "sans-serif",
            border: "unset",
            paddingTop: 50,
            fontWeight: "bold"
            // fontSize: "medium"
          }}
        >
          Create the configuration from the original dataset as in the 'Dataset
          and server' tab.
        </div>
        <textarea
          readOnly
          rows="25"
          cols="180"
          value={text}
          style={{
            fontFamily: "sans-serif",
            border: "unset",
            paddingTop: 10
          }}
        />
      </div>
    );
  }
}
